	position: relative;
	
	@tree-width: 300px;
	.tree-side {
		position: absolute;
		width: @tree-width;
		top: 0;
		left: 0;
		bottom: 0;
		box-sizing: border-box;
	}
	
	.tree-spot {
		position: absolute;
		width: 100%;
		bottom: 0;
		left: 0;
		top: 80px;
		box-sizing: border-box;
		height: 700px;
	}
	
	.controls {
		position: absolute;
		width: 100%;
		top: 0;
		left: 0;
		height: 80px;
		box-sizing: border-box;
	}
	
	.properties-side {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: @tree-width + 40px;
	}
	
	.option-properties {
		label {
			display: block;
			margin-bottom: .8em;
		}
		
		input[type=text] {
			display: block;
			box-sizing: border-box;
			width: 100%;
			padding: 3px 6px;
			background: transparent;
			border-radius: 4px;
			border: solid 1px black;
		}
		
		input[type=radio] {
			display: inline-block;
			width: auto;
			margin-right: 20px;
		}
		
		select {
			display: block;
			box-sizing: border-box;
			width: 100%;
			padding: 3px 6px;
			background: transparent;
			border-radius: 4px;
			border: solid 1px black;
		}
	}
}
.menu-editor-svgs {
	height: 0;
	overflow: hidden;
}

.page-editor-menu-selector {
	float: right;
	width: 300px;
}

.webhandle-page-editor-file-browser {
	.file-list {
		.directory-item, .file-item {
			.type-indicator {
				vertical-align: middle;
				padding-right: 10px;
			}

			.delete {
				visibility: hidden;
				cursor: pointer;
			}

			&:hover {
				.delete {
					visibility: visible;
				}
			}
		}

	}
}
